import React, { useEffect, useState } from 'react';
import { Button, Container, Row, Table } from 'reactstrap';
import { Repe } from '../Repetition/Repe';

export const Serie = ({ pNumSeries, pNumReps, editable, size, pWeigth }) => {
  const [numSeries, setNumSeries] = useState(Array.from(Array(pNumSeries).keys()));
  const [isEditable, setIsEditable] = useState(editable);
  const [weight, setWeigth] = useState(pWeigth);
  const [numRepes, setNumRepes] = useState(pNumReps);
  const [title, setTitle] = useState(size);

  useEffect(() => {
    setIsEditable(editable);
    setNumSeries(Array.from(Array(pNumSeries).keys()));
    setWeigth(pWeigth);
  }, [editable, pNumSeries, pNumReps, pWeigth, size]);

  const handleOnSave = () => {
    alert('Saved!!');
  };

  return (
    <Container>
      <Row>
        <h2>This is a {title} size serie</h2>
      </Row>
      <Row>
        <Table>
          <thead>
            <tr>
              <th>#</th>
              <th>#repetitions</th>
              <th>weight</th>
            </tr>
          </thead>
          <tbody>
            {numSeries.map(r => (
              <Repe
                key={r}
                id={r}
                pNumSeries={numRepes}
                pWeigth={weight}
                editable={editable}
              ></Repe>
            ))}
          </tbody>
        </Table>
        <Button onClick={handleOnSave}>Save</Button>
      </Row>
    </Container>
  );
};
